import mixins from "@/assets/js/mixins.js";
export default {
  mixins: [mixins.charts],
  data() {
    return {     
      type: "101",
      columns: [
        {
          title: "状态",
          key: "MeterTime",
          width: 100,
          align: "center",
          render: (h, params) => {
            var isError = this.getState(params.row.MeterValue);
            return h("div", [
              h(
                "Tag",
                {
                  props: {
                    color: isError ? "red" : "green"
                  },
                  on: {}
                },
                isError ? "异常" : "正常"
              )
            ]);
          }
        },
        {
          title: "体温(℃)",
          key: "MeterValue",
          align: "center"
        },
        {
          title: "时间",
          key: "MeterTime",
          width: 200,
          align: "center"
        }
      ]
    };
  },
  computed: {
    
  },
  mounted() {},
  methods: {
    
    getPieDataRender(res) {
      this.chartPieOption.series[0].data = [
        { value: res.nomal, name: "正常" },
        { value: res.notnomal, name: "异常" }
      ];
    },
    getDataRender(res) {
      var data = res.data;
      this.healthWarn.max = res.max;
      this.healthWarn.min = res.min;
      this.chartLineOption.yAxis.max = 42;
      this.chartLineOption.yAxis.min = 30;
      this.chartLineOption.xAxis.data = data.map(t => {
        return t.MeterTime;
      });
      this.chartLineOption.legend.data = ["体温"];
      this.chartLineOption.series = [
        {
          name: "体温",
          type: "line",
          data: data.map(t => {
            return {
              value: t.MeterValue,
              itemStyle: {
                normal: {
                  color: (() => {
                    if (this.getState(t.MeterValue)) {
                      return "red";
                    }
                  })()
                }
              }
            };
          })
        }
      ];
    }
  }
};



